<template>
  <div>
    <!--begin::Notice-->
    <div
      class="alert alert-custom alert-white alert-shadow gutter-b mt-1"
      role="alert"
    >
      <div class="alert-icon alert-icon-top">
        <div>
          <img
            style="height: 48px"
            :src="
              '../../media/logos/task_logo/' +
              this.$store.state.taskinfo.name +
              '.jpg'
            "
          />
        </div>
      </div>

      <div class="alert-text alert-text-center">
        <p style="margin-top: 15px">
          <span
            class="label label-inline label-pill label-primary label-rounded mr-2"
          >
            DESCRIPTION:
          </span>
          {{ this.$store.state.taskinfo.description }}
        </p>
      </div>
    </div>
    <!--end::Notice-->

    <!--begin: methods_info-->
    <div class="card card-custom card-stretch gutter-b">
      <div class="card-header border-0 py-5 mt-5">
        <p class="display-4 font-weight-bolder text-dark">
          {{ this.display_name }}
        </p>
        <br />
      </div>
      <div class="card-text ml-10 mr-10 mb-10">
        <p>
          <b-badge variant="primary"> Introduction </b-badge>

          <template v-for="(desc, desc_index) in this.method_desc_list">
            <p v-bind:key="'method_desc_' + desc_index" class="mt-2 ml-3">
              <span class="font-weight-bolder">{{
                method_list[desc_index]
              }}</span>
              {{ desc.desc }}
            </p></template
          >
        </p>

        <p class="ml-3" v-if="display_type == 'transformations'">
          Click
          <a
            :href="download_link"
            :download="download_name"
            style="color: rgba(0, 143, 251, 0.85)"
            >here</a
          >
          to download the transformed data
        </p>

        <p>
          <b-badge variant="primary" v-if="display_type == 'transformations'">
            Contributor
          </b-badge>

          <template v-for="(item, index) in this.contributors">
            <div v-bind:key="'contributor_name' + index" class="mt-2">
              <b-button
                v-b-toggle="'collapse-' + index"
                variant="outline"
                style="color: rgba(0, 143, 251, 0.85)"
                size="sm"
                >{{ item.name }}</b-button
              >
              <b-collapse :id="'collapse-' + index">
                <b-card class="col-3 p-0 b-0 m-0">
                  <b-card-body class="p-0 b-0 m-0">
                    <p class="font-weight-bolder m-0">Github:</p>
                    <a :href="item.github" target="_blank"
                      >{{ item.github }}
                    </a>
                  </b-card-body>
                </b-card>
              </b-collapse>
            </div>
          </template>
        </p>
      </div>
    </div>
    <!--end: methods_info-->

    <!--start: method_chart -->
    <div class="card card-custom gutter-b">
      <div class="card-body mr-10">
        <BarChart
          v-bind:result_info="this.result_info"
          :display_type="this.display_type"
          :name="this.display_name"
          :metric="metric"
          :dataset="dataset"
        ></BarChart>
      </div>
    </div>
    <!--end: method_chart -->

    <!-- start human_eval_chart-->
    <div
      v-if="display_type == 'transformations'"
      class="card card-custom gutter-b"
    >
      <HumanEvalChart
        class="mt-3"
        :display_type="this.display_type"
        :name="this.display_name"
        :dataset="dataset"
      ></HumanEvalChart>
    </div>
    <!-- end human_eval_chart -->
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import HumanEvalChart from "@/view/content/widgets/list/widget-human-eval-chart.vue";
import BarChart from "@/view/content/widgets/list/widget-bar-chart.vue";
import { SET_TASKINFO } from "@/core/services/store/taskinfo.module";
import { SET_BREADCRUMB } from "@/core/services/store/breadcrumbs.module";

export default {
  name: "chartdisplay",
  components: {
    HumanEvalChart,
    BarChart,
  },

  beforeCreate() {
    var display_type = this.$route.query.display_type;
    if (display_type != "models" && display_type != "transformations") {
      alert(
        "Unable to display in " +
          display_type +
          " mode, please wait for subsequent updates."
      );
      this.$router.push("/");
    }
  },

  created() {
    var task_desc_json = require("../../../public/Tasks/" +
      this.$route.query.task +
      "/task_description.json");
    this.$store.dispatch(SET_TASKINFO, task_desc_json);
    this.$store.dispatch(SET_BREADCRUMB, [
      { title: this.$store.state.taskinfo.full_name },
    ]);
  },
  data() {
    var display_name = this.$route.query.name;
    var display_type = this.$route.query.display_type;
    var task_name = this.$route.query.task;
    var metric = this.$route.query.metric;
    var dataset = this.$route.query.dataset;
    var trans_type = this.$route.query.trans_type;
    var result_json = require("../../../public/Tasks/" +
      task_name +
      "/results/" +
      dataset +
      "/" +
      trans_type +
      "_" +
      metric +
      ".json");
    var info_array = null;
    var contributors = null;
    var download_link = null;
    if (display_type == "transformations") {
      info_array = this.get_info(result_json, display_name);
      contributors = info_array[0];
      download_link = info_array[1];
    }

    var method_list = display_name.split("_");
    var download_name = `${dataset}_trans_${display_name}.json`;

    // Get multiple transformations description.

    return {
      display_name: display_name,
      display_type: display_type,
      method_list: method_list,
      method_desc_list: [],
      contributors: contributors,
      result_info: result_json,
      dataset: dataset,
      metric: metric,
      download_link: download_link,
      download_name: download_name,
      task_name: task_name,
    };
  },

  computed: {
    ...mapGetters(["layoutConfig"]),

    config() {
      return this.layoutConfig();
    },
  },
  mounted() {
    for (var index in this.method_list) {
      var method_name = this.method_list[index];
      var method_desc_json = "";
      try {
        method_desc_json = require("../../../public/Tasks/" +
          this.task_name +
          "/" +
          this.display_type +
          "/" +
          method_name +
          ".json");
      } catch (err) {
        // console.log(err);
        method_desc_json = {
          desc:
            "There is no description about " +
            method_name +
            ", please wait for the subsequent update.",
          contributor: [],
        };
      }
      this.method_desc_list.push(method_desc_json);
    }
  },
  methods: {
    /**
     * Reset config
     */
    reset(event) {
      event.preventDefault();
      // remove existing saved config
      localStorage.removeItem("config");
      window.location.reload();
    },

    get_info(result_json, target_name) {
      var contributors = new Array();
      var contributor_name = new Array();
      var download_link = null;
      for (var model_index in result_json) {
        try {
          if (
            download_link == null &&
            result_json[model_index].attack_results[target_name]
              .trans_download_link != undefined
          ) {
            download_link =
              result_json[model_index].attack_results[target_name]
                .trans_download_link;
          }
        } catch {
          download_link = null;
        }

        try {
          if (
            contributor_name.indexOf(
              result_json[model_index].attack_results[target_name].contributor
                .name
            ) == -1
          ) {
            contributors.push(
              result_json[model_index].attack_results[target_name].contributor
            );
            contributor_name.push(
              result_json[model_index].attack_results[target_name].contributor
                .name
            );
          }
        } catch {
          continue;
        }
      }
      return [contributors, download_link];
    },
  },
};
</script>
<style>
#p {
  text-indent: 2em;
}
</style>
