<template>
  <div id="chart" class="card-body pt-0 pb-3">
    <apexchart
      type="bar"
      height="auto"
      width="90%"
      title="results"
      :options="chartOptions"
      :series="series"
    ></apexchart>
  </div>
</template>

<script>
import VueApexCharts from "vue-apexcharts";
export default {
  name: "widget-bar-chart",
  props: ["result_info", "display_type", "name", "metric", "dataset"],
  components: {
    apexchart: VueApexCharts,
  },

  data() {
    var x_list = new Array();
    var ori_list = new Array();
    var trans_list = new Array();
    var result_array = this.get_target_result(
      this.result_info,
      this.display_type,
      this.name
    );
    var x_min =
      result_array.sort(function (item1, item2) {
        return item1.trans - item2.trans;
      })[0].trans - 3;

    for (var trans_index in result_array) {
      var item = result_array[trans_index];
      x_list.push(item.name);
      ori_list.push(parseFloat(item.ori).toFixed(2));
      trans_list.push(parseFloat(item.trans).toFixed(2));
    }
    // console.log(result_info)
    return {
      series: [
        {
          name: "ori",
          data: ori_list,
        },
        {
          name: "trans",
          data: trans_list,
        },
      ],
      chartOptions: {
        title: {
          text: "Attack Results (" + this.metric + ") in " + this.dataset,
          align: "mid",
          margin: 10,
          offsetX: 10,
          offsetY: 10,
          floating: false,
          style: {
            fontSize: "20px",
            fontWeight: "bold",
            fontFamily: undefined,
            color: "#263238",
          },
        },
        chart: {
          type: "bar",
        },
        legend: { fontSize: "13px" },
        plotOptions: {
          bar: {
            horizontal: true,
            dataLabels: {
              position: "top",
            },
          },
        },
        dataLabels: {
          enabled: true,
          style: {
            fontSize: "12px",
            colors: ["#fff"],
          },
        },
        stroke: {
          show: true,
          width: 1,
          colors: ["#fff"],
        },
        xaxis: {
          categories: x_list,
          min: x_min,
        },
        yaxis: {
          labels: {
            style: {
              fontSize: "12px",
            },
          },
        },
      },
    };
  },
  methods: {
    get_target_result(result_info, display_type, name) {
      var target_result = new Array();
      for (var model of result_info) {
        if (display_type == "transformations") {
          for (var trans_index in model.attack_results) {
            if (trans_index == name) {
              target_result.push({
                name: model.model,
                ori: model.attack_results[trans_index].ori,
                trans: model.attack_results[trans_index].trans,
              });
            }
          }
        } else {
          if (model.model == name) {
            for (var trans_name in model.attack_results) {
              target_result.push({
                name: trans_name,
                ori: model.attack_results[trans_name].ori,
                trans: model.attack_results[trans_name].trans,
              });
            }
          }
        }
      }
      target_result = target_result.sort(function (item1, item2) {
        return (item2.ori - item2.trans) - (item1.ori - item1.trans);
      });
      target_result = target_result.slice(0, 10);
      //       var sorted_result_info = result_array.sort(function (item) {
      //   if (item.name.indexOf("Subpopulation")) return -1;
      //   else return item.ori - item.trans;
      // });
      // console.log(result_array);
      // var result_info = sorted_result_info.slice(0, 10);
      // var x_min = get_json_min(result_info);

      return target_result;
    },
  },
};
</script>
